// main imports
import React, { Component } from "react";
import logo from "./logo.svg";

// styles
import "./App.css";

// components
import PoseNet, { PoseNetReplay, PoseNetMatch } from "./components/Posenet";

class App extends Component {
  state = {
    recording: [],
    videoRecording: [],
    replay: false,
    front: false,
    video: true,
    multipose: false,
    stop: false,
    mode: "compete"
  };
  onToggle = this.onToggle.bind(this);
  onToggleFrontCamera = this.onToggleFrontCamera.bind(this);
  onToggleVideoFeed = this.onToggleVideoFeed.bind(this);
  onTracePose = this.onTracePose.bind(this);
  onTraceVideo = this.onTraceVideo.bind(this);
  onToggleMultiPose = this.onToggleMultiPose.bind(this);
  onStartRecord = this.onStartRecord.bind(this);
  onStopRecord = this.onStopRecord.bind(this);

  onToggle(e) {
    this.setState({ replay: !this.state.replay, mode: e });
  }

  onToggleFrontCamera() {
    this.setState({ front: !this.state.front });
  }

  onToggleVideoFeed() {
    this.setState({ video: !this.state.video });
  }

  onToggleMultiPose() {
    this.setState({ multipose: !this.state.multipose });
  }

  onTracePose(e) {
    this.setState({ recording: e });
  }

  onTraceVideo(e) {
    console.log("tracing video");
    this.setState({ videoRecording: e });
  }

  onStartRecord() {
    this.setState({ record: true });
  }

  onStopRecord() {
    this.setState({ record: false });
  }

  render() {
    return (
      <div className="App">
        <div
          style={{
            minHeight: 200,
            display: "flex",
            justifyContent: "flex-start",
            alignItems: "center",
            flexDirection: "column",
            overflow: "hidden"
          }}
        >
          <br />
          <div
            style={{
              width: "80%",
              display: "flex",
              flexDirection: "column",
              justifyContent: "space-evenly"
            }}
          >
            <div>
              <button
                onClick={() => this.onToggle("record")}
                style={{
                  padding: "8px 15px",
                  backgroundColor:
                    this.state.recording.length === 0 ? "salmon" : "white",
                  color: this.state.recording.length === 0 ? "white" : "black"
                }}
              >
                RECORD
              </button>
              <button
                onClick={() => this.onToggle("replay")}
                style={{
                  padding: "8px 15px",
                  backgroundColor:
                    this.state.recording.length !== 0 ? "salmon" : "white",
                  color: this.state.recording.length !== 0 ? "white" : "black"
                }}
              >
                REPLAY
              </button>
              <button
                onClick={() => this.onToggle("compete")}
                style={{
                  padding: "8px 15px",
                  backgroundColor:
                    this.state.recording.length !== 0 ? "salmon" : "white",
                  color: this.state.recording.length !== 0 ? "white" : "black"
                }}
              >
                COMPETE
              </button>
            </div>

            <br />
            <code>recorded frames: {this.state.recording.length}</code>
          </div>

          <hr style={{ width: "100%" }} />

          <div
            style={{
              display: this.state.mode === "record" ? "flex" : "none",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center"
            }}
          >
            <div
              style={{
                width: "80%",
                display: "flex",
                justifyContent: "space-evenly"
              }}
            >
              <button onClick={this.onToggleFrontCamera}>
                {this.state.front ? "front" : "rear"} camera
              </button>
              <button onClick={this.onToggleVideoFeed}>
                video {this.state.video ? "on" : "off"}
              </button>
              <button onClick={this.onToggleMultiPose}>
                {this.state.multipose ? "multi-" : "single "}pose
              </button>
              <button onClick={this.onStartRecord}>start recording</button>
              <button onClick={this.onStopRecord}>stop recording</button>
            </div>
            <br />
            <code
              style={{
                fontSize: 10,
                color: "grey",
                width: "70%"
              }}
            >
              *video toggle and single/multipose toggles don't seem to work
              while camera is already running. please toggle between cameras to
              make it work.
            </code>
          </div>
        </div>

        <div
          style={{ display: this.state.mode === "replay" ? "block" : "none" }}
        >
          <PoseNetReplay
            poseRecords={this.state.recording}
            poseVideo={this.state.videoRecording}
            showVideo={true}
          />
        </div>
        {this.state.mode === "record" ? (
          <PoseNet
            videoWidth={600}
            //videoHeight={500}
            mobileNetArchitecture={1.01}
            outputStride={8}
            loadingText={"Loading..."}
            frontCamera={this.state.front}
            getPoseRecords={this.onTracePose}
            getVideoRecords={this.onTraceVideo}
            showVideo={this.state.video}
            algorithm={this.state.multipose ? "multi-pose" : "single-pose"}
            record={this.state.record}
            recordVideo
          />
        ) : (
          ""
        )}
        {this.state.mode === "compete" ? (
          <PoseNetMatch
            poseRecords={this.state.recording}
            poseVideo={this.state.videoRecording}
          />
        ) : (
          ""
        )}
      </div>
    );
  }
}

export default App;
